<script>
export default {
  name: "About",
  props: {},
  methods: {
  },
  computed: {
    project() {
      return this.$root.$data.about[0]
    },
    date() {
      let date = this.project.date.split("T")[0];
      let parts = date.split("-");
      return parts[1] + "/" + parts[2] + "/" + parts[0];
    }
  },
  data() {
    return {};
  }
};
</script>

